	import LoadMores from '../../components/uni-load-more/uni-load-more.vue'
	import { requestip } from '../../pages/api/api.js'
	export default {
		components:{
			LoadMores
		},
		data(){
			return{
				Tabindex:0,
				descmenu:[
					{name:"综合",num:0,num2:0},
				    {name:"价格",num:4,num2:5},
					{name:"销量",num:6,num2:7}
				],
				product:[1,2,3,4,5],
				fixmenu:false,
				moneydata:'',
				opt_id:'',
				descnum:0,
				propsData:'',
				propsData2:[],
				pages:1,
				flexblean:true,
				imgicon1:"../../static/hengx2.png",
				imgicon2:"../../static/shu2x.png",
				propsDb:true,
				showloadMore:false,
				loadMoreText:"加载中...",
				riseBlean:false,
			}
		},
		props:{
			propsdata:{
				type:Object,
				defualt:function(e){
					return {}
				}
			},
			propsdata2:{
				type:Object,
				defualt:function(e){
					return {}
				}
			},
			propsDatas2:{
				type:Object,
				defualt:function(e){
					return {}
				}
			}
		},
		methods:{
			// 排序点击获取数据
			menuclicks(i,n){
				this.Tabindex=n;
				// 排序方式
				this.descnum=i;
				uni.setStorageSync('descnum',i);
				// this.propsDb=false;
				this.riseBlean=!this.riseBlean;
				// 获取opt_id;
				this.opt_id=uni.getStorageSync('optId');
				this.propsDatas2=[];
				this.showloadMore=true;
				uni.request({
					url: requestip()+'/thirdreturn/index/classgoodsbysort?class_id='+this.opt_id+'&page='+this.pages+'&is_sub=0&prent_id='+this.opt_id+'&sort_type='+this.descnum,
					method: 'GET',
					data: {},
					success: res => {
						console.log("主页的排序数据2：",res.data);
						this.propsDatas2=res.data.goods_search_response.goods_list;
						this.showloadMore=false;
					},
					fail: () => {},
					complete: () => {}
				});
			},
			// 小菜单跳转数据
			goodfoodList(t,i){
				var allId=uni.getStorageSync('optId');
				uni.setStorageSync('goodLike',t.opt_id);
				if(t.topTid){
					uni.navigateTo({
						url:"../../pages/gooList/gooList?id="+allId+'&num=1&name='+t.name
					})
				}else{
					uni.navigateTo({
						url:"../../pages/gooList/gooList?id="+t.opt_id+'&num=0&name='+t.name
					})
				}
					
			},
			goodLm(i){
				uni.navigateTo({
					url:'../../pages/productmess/productmess?id='+i.goods_id+'&sheng='+i.upgrade_rebate+'&ling='+i.now_rebate
				})
			},
			// 布局切换
			flexgood(){
				this.flexblean=!this.flexblean;
// 				uni.pageScrollTo({
// 					duration:100,
// 					scrollTop:0
// 				})
			},
		    rules(){
				uni.navigateTo({
					url:'../../pages/rule/rule'
				})
			}
		},
		// 在微信和app中可以用h5无效果
		onPageScroll(e) {
			// console.log(e);
			if(e.scrollTop>=240){
				 this.fixmenu=true;
			}else{
				this.fixmenu=false;
			}
		},
		onLoad() {
			
		},
		onShow() {
			console.log('69696');
		},
	}
