	import GoodList from '../../components/goodlist/goodlist.vue'
	import LoadMores from '../../components/uni-load-more/uni-load-more.vue'
	import BackTop from '../../components/backtop/backTop.vue'
	import uniCountdown from '@/components/uni-countdown/uni-countdown.vue'
	import GoodFlex from '@/components/goodflex/goodflex.vue';
	// 引入api接口
	import {requestip,headerList } from '../api/api.js'
	export default {
		components:{
			GoodList,
			LoadMores,
			BackTop,
			uniCountdown,
			GoodFlex
		},
		data() {
			return {
				tabmenus:'',
				propsdata:'',
				propsdata2:'',
				propsData2:[],
				tabIndex:0,
				pages:1,
				showloadMore:false,
				loadMoreText:"加载中...",
				allList:false,
				topblean:false,
				descblean:false,
				banimgs:'',
				bannerShow:false,
				copydatas:''
			}
		},
		onLoad:function(e) {
		    this.indexInit(e);
			uni.setStorageSync('e',e);
		},
		methods: {
			indexInit(e){

			 uni.showLoading({
				title:'加载中...'
			})

			this.selections();
			// 初始化数据
			this.propszero();
			// 排序初始化
			uni.setStorageSync('descnum',0);
			console.log('页面的数据传递',e);
			// 邀请页面传值
			// 扫二维码传值
			var opId1=uni.getStorageSync('parentId');
			var io=uni.getStorageSync('parentId1');
			console.log("io:",io)
			var oparentid=opId1?e.oparentid:opId1;
			// 渠道
			var channel=e.channel?e.channel:1;
			uni.setStorageSync('channel',channel);
			// 设置页面传的值
			uni.setStorageSync('parentId',oparentid);
			// console.log('邀请页面值:',friendId);
			// 目录请求
			this.tabmenuList();
			// 初始化数据
			
			uni.setStorageSync('optId',1)
			// 返回Id
			var backId=uni.getStorageSync('optId');
			uni.setStorageSync('backoptId',backId);
			uni.request({
				url: requestip()+'/product/index/class',
				method: 'GET',
				data: {},
				success: res => {
					console.log("初始化数据：",res.data)
					this.propsdata=res.data[0].child.splice(0,9);
					this.propsdata.push({name:'查看更多',topTid:true,img:'../../static/more.png'});
					this.propsdata2=res.data;
					this.allList=true;
					uni.hideLoading();
				}
			});
			},
            // 菜单目录
            tabmenuList(){
				   uni.request({
					url: requestip()+'/thirdreturn/index/goodsopt',
					method: 'GET',
					data: {},
					success: res => {
						console.log("菜单目录:",res.data);
						this.tabmenus=res.data.goods_opt_get_response.goods_opt_list;
						var unOne={opt_name:'精选',opt_id:1}
						this.tabmenus.unshift(unOne);
					}
				   });
            },
			// 点击菜单获取数据
		   tapTab(t,i){
				   this.tabIndex=i;
				   var optId=t.opt_id;
				   this.propsdata='';
				   this.propsdata2='';
				   uni.showLoading({
				   	title:'加载中...'
				   })
				   uni.setStorageSync('optId',optId);
				   // 排序初始化
				   uni.setStorageSync('descnum',0);
				   // 返回Id
				   uni.setStorageSync('backoptId',optId);
				   console.log(this.tabIndex);
				   console.log("商品的opId:",optId);
				   this.propsData2='';
				   // 初始化数据
				   this.propszero();
				   uni.request({
					url: requestip()+'/product/index/class',
					method: 'GET',
					data: {},
					success: res => {
						console.log(res.data)
						if(i>0){
							this.propsdata=res.data[i-1].child.splice(0,9);
							this.propsdata.push({name:'查看全部',topTid:true,img:'../../static/more.png'});
							
						}
						else if(i==1){
							this.propsdata=res.data[1].child.splice(0,9);
							this.propsdata.push({name:'查看全部',topTid:true,img:'../../static/more.png'});
						}
						this.propsdata2=res.data;
						uni.hideLoading();
					},
					fail: () => {},
					complete: () => {}
				   });
				  uni.pageScrollTo({
						scrollTop: 0,
						duration: 10
					});
	      },
		  // 滚动
		  changeTab(e){
			  console.log("滚动;",e);
		  },
		  // input 聚焦跳转
		  searchbox(){
			  uni.navigateTo({
			  	url:'../searchinput/searchinput'
			  })
		  },
		  // 分类跳转
		  feleNas(){
			  uni.navigateTo({
			  	url:'../feilei/feilei'
			  })
		  },
		  // 精选请求
		  selections(){
			  uni.request({
			  	url: requestip()+'/thirdreturn/index/themelist?page=1',
			  	method: 'GET',
			  	data: {},
			  	success: res => {
			  		console.log('初始化banner数据:',res.data.theme_list_get_response.theme_list)
			  		this.banimgs=res.data.theme_list_get_response.theme_list
			  	}
			  });
		  },
		  // 初始化数据
		  propszero(){
		  	this.opt_id=uni.getStorageSync('optId');
		  	uni.request({
		  		url: requestip()+'/thirdreturn/index/classgoods?class_id='+this.opt_id+'&page=2&is_sub=1&prent_id='+this.opt_id,
		  		method: 'GET',
		  		data: {},
		  		success: res => {
		  			console.log("zy初始化数据",res.data);
		  			this.propsData2=res.data[1].goods_search_response.goods_list;
		  		}
		  	});
		  },
		  // free
		  freeships(m){
			  uni.navigateTo({
			  	url:'../freeship/freeship?name='+m
			  })
		  },
		  // 关闭剪切搜索
		  closeCopy(){
			  this.bannerShow=false;
			},
			sNavto(n){
				uni.navigateTo({
					url:'../searchinput/searchinput?name='+n
				})
			},
			// 关闭下拉
			sotpre(){
				setTimeout(()=>{
					var e=uni.getStorageSync('e');
					this.indexInit(e);
					uni.stopPullDownRefresh();
				},300)
			},
		},
		onShow() {
			console.log("backonpress");
			var that=this;
			var backopid=uni.getStorageSync('backoptId');
			uni.setStorageSync('optId',backopid);
			uni.getClipboardData({
				success: function (res) {
					console.log(res.data);
					if(res.data==uni.getStorageSync('copyData')){
						that.bannerShow=false;
					}
					else if(res.data!='' && res.data!=null){
						that.bannerShow=true;//开关
						uni.setStorageSync('copyData',res.data);
						that.copydatas=res.data;
						console.log('0000000');
					}
					else{
						that.bannerShow=false;
					}
				}
			});
		},
		onShareAppMessage() {
			return{
				  title: 'shopcar',
				  path: 'pages/index/index'
			}
		},
		destroyed() {
		    
		},
		onPageScroll(e) {
		   if(e.scrollTop>700){
			   this.topblean=true;
		   }else{
			   this.topblean=false;
		   }
		},
		// 触底
		onReachBottom() {
			this.pages+=1;
			this.showloadMore=true;
			var otp_id=uni.getStorageSync('optId');
			var descnum=uni.getStorageSync('descnum');
			uni.request({
				url: requestip()+'/thirdreturn/index/classgoods?class_id='+otp_id+'&page='+this.pages+'&is_sub=0&prent_id='+otp_id+'&sort_type='+descnum,
				method: 'GET',
				data: {},
				success: res => {
					console.log("触底数据：",res.data);
					for(var i=0;i<res.data.goods_search_response.goods_list.length;i++)
					{
						this.propsData2.push(res.data.goods_search_response.goods_list[i]);
					}
		           this.showloadMore=false;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 下拉刷新监听函数
		onPullDownRefresh() {
			console.log("onPullDownRefresh")
			this.sotpre();
		},
	}
